.nav {
   $parent: &;
   @include sm {
      flex-direction: column;
      justify-content: center;
      position: absolute;
      top: 80px;
      left: 0;
      width: 100%;
      height: 100vh;
      z-index: 10;
      padding-bottom: 80px;
      overflow: auto;
      opacity: 0;
      border: 0;
      visibility: hidden;
      transform: translate3d(0, 100px, 0);
      transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
      background: #fff;
      text-align: center;
      box-shadow: inset 0 0 25px rgba($color1, .3); }
   @include xs {
      top: 65px;
      padding-bottom: 65px; }
   &.is-active {
      opacity: 1;
      visibility: visible;
      transform: translate3d(0, 0, 0);
      transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease; }

   &__body {
      height: 100%;
      @extend .container; }

   &__list {
      display: flex;
      align-items: center;
      height: 100%;
      @include sm {
         flex-direction: column;
         justify-content: center;
         align-items: center;
         overflow: auto;
         height: 100%;
         padding: 0; }
      &-item {
         position: relative;
         &:not(:last-child) {
            margin-right: 60px; } }
      &-link {
         display: block;
         @include f('ub400');
         font-size: 16px;
         line-height: 16px;
         color: $color2;
         transition: .3s ease-in-out;
         @include sm {
            position: relative;
            z-index: 1;
            display: inline-block;
            padding: 0 15px;
            color: $color1;
            font-size: 16px;
            background: $white; }
         &:hover {
            color: $white; }
         &_login {
            position: relative;
            padding: 14px 35px 14px 20px;
            min-width: 160px;
            border-radius: 4px;
            border: 1px solid rgba($white, .5);
            color: $white;
            text-align: center;
            &:hover {
               border-color: $white;
               i {
                  color: $white;
                  animation: smallMoveRight .3s ease-in-out; } }
            i {
               position: absolute;
               top: calc(50% - 7px);
               right: 20px;
               font-size: 16px;
               color: rgba($white, .5);
               transition: .3s ease-in-out; } } } } }

.nav__btn {
   $parent: &;
   display: none;
   position: relative;
   top: -5px;
   border: 0;
   outline: 0;
   padding: 0;
   z-index: 11;
   @include sm {
      display: block; }

   &.is-active span {
      color: $color1; }

   &-burger {
      display: block;
      margin: auto;
      width: 40px;
      height: 40px;
      cursor: pointer; }
   &-lines {
      display: block;
      width: 40px;
      height: 3px;
      &:after, &:before {
         width: 40px;
         height: 3px; }
      position: relative;
      transform: translateY(25px);
      background-color: $color1;
      transition: all .3s;
      #{$parent}.is-active & {
         background: rgba(255, 255, 255, 0); }
      &:before {
         content: "";
         position: absolute;
         left: 0;
         bottom: 10px;
         background-color: $color1;
         transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
      &:after {
         content: "";
         position: absolute;
         left: 0;
         top: 10px;
         background-color: $color1;
         transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
      #{$parent}.is-active & {
         &:after {
            top: 0;
            transform: rotate(45deg);
            transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); }
         &:before {
            bottom: 0;
            transform: rotate(-45deg);
            transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); } } } }
